import Project from '../../domain/Project'
import {generate} from '../helpers'
import {ORGANIZATION_1, ORGANIZATION_2} from './organizations'

const build = (t: Partial<Project>): Project => {
  return generate(t, Project)
}

export const PROJECT_ZOO = build({
  name: 'Zoo',
  dateStart: new Date(Date.UTC(2021, 2, 8)),
  dateEnd: new Date(Date.UTC(2021, 6, 4)),
  organizationId: ORGANIZATION_1.id
})

