import ProjectParticipation, {State} from '../../domain/ProjectParticipation'
import {generate} from '../helpers'
import {ORG_1_UNRELATED_USER, ORG_1_USER} from './persons'
import {PROJECT_ZOO} from './projects'

const build = (t: Partial<ProjectParticipation>): ProjectParticipation => {
  return generate(t, ProjectParticipation)
}

export const PARTICIPATION_ZOO_1 = build({
  personId: ORG_1_USER.id,
  projectId: PROJECT_ZOO.id,
  capacity: .6,
  dateEnd: new Date(Date.UTC(2021, 3, 8)),
  dateStart: new Date(Date.UTC(2021, 3, 1)),
  fromParticipationId: null,
  state: State.APPROVED
})

export const PARTICIPATION_ZOO_2 = build({
  personId: ORG_1_UNRELATED_USER.id,
  projectId: PROJECT_ZOO.id,
  capacity: 1,
  dateEnd: new Date(Date.UTC(2021, 3, 8)),
  dateStart: new Date(Date.UTC(2021, 3, 1)),
  fromParticipationId: null,
  state: State.APPROVED
})